/* Profile.css */

.profile-container {
  display: flex;
  justify-content: center;
  align-items: flex-end; /* Align content to the bottom */
  min-height: 100vh; /* Use the full viewport height for vertical centering */
  overflow-y: auto; /* Add vertical scrolling when content exceeds viewport height */
  position: relative; /* Add relative positioning to the container */
}

.profile {
  display: flex;
  align-items: flex-start; /* Align content to the top */
  padding: 40px;
  text-align: center;
  position: relative; /* Add relative positioning to allow absolute positioning of the image */
}

.profile-image {
  width: 560px;
  height: 800px; /* Set a fixed height to make it rectangular */
  margin-right: 20px;
  position: absolute; /* Position the image absolutely */
  bottom: 0; /* Align the image to the bottom of the container */
  left: 0; /* Align the image to the left side */
}

.profile-info {
  flex: 1; /* Let the info section grow to fill the available space */
  font-family: 'Helvetica Neue', sans-serif;
  text-align: left; /* Align text from left to right */
  padding-left: 600px; /* Adjust the padding to keep space for the image */
  margin-top: -20px; /* Move the info section upwards */
  margin-bottom: 300px;
}

.profile-info h2 {
  font-size: 45px; /* Increase the font size for h2 */
  font-weight: bold;
  text-align: left; /* Align heading text from left to right */
}

.profile-info p {
  font-size: 22px; /* Increase the font size for the info text */
  text-align: left; /* Align paragraph text from left to right */
}

/* Profile.css */

.contact-icons {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-top: 10px; /* Move the icons and text upwards */
}

.contact-icons a {
  text-decoration: none;
  color: #333;
  font-size: 30px;
}

.contact-icons a:hover {
  color: yellow;
}

/* Responsive Styles */
@media screen and (max-width: 768px) {
  .profile-container {
    align-items: center; /* Center content vertically on smaller screens */
  }

  .profile {
    flex-direction: column; /* Stack content vertically on smaller screens */
    text-align: center; /* Center text on smaller screens */
    padding: 20px; /* Reduce padding for smaller screens */
  }

  .profile-image {
    width: 250px; /* Reduce image size for smaller screens */
    height: auto; /* Maintain aspect ratio */
    position: static; /* Remove absolute positioning */
    margin: 20px 0; /* Add margin to separate image and text */
  }

  .profile-info {
    padding: 0; /* Remove padding */
    margin-top: 0; /* Remove margin */
  }

  .profile-info h2 {
    font-size: 30px; /* Reduce font size for h2 */
  }

  .profile-info p {
    font-size: 18px; /* Reduce font size for text */
  }

  .contact-icons {
    margin-top: 10px; /* Adjust margin */
  }
}